.App-header {
  min-width: 100%;
  max-height:300px;
  /* min-height: 100vh; */
  background-color: #1b1d24;
  display: inline-flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
  font-size: calc(2px + 2vmin);
  color: rgb(230, 230, 230);
}


#title {
  padding-left: 9.25%;
}

#logo{
  width: 200px;
  height: 150px;
  padding-left: 0.5%;
}

#Hamburger-icon{
  width: 150px; 
  height: 150px;
  padding-left: 10%;
}

body{
  background-color: #1f232b;
  
}
.Intro-part{
  display: inline-flex;
}
.Deliver-text{
  font-size: calc(40px + 2vmin);
  color: rgb(207, 141, 19);
  margin-top: 15%;
  margin-left: 8%;
  font-weight: 550;
  font-family:  'Allan';
}

.Deliver-text:hover{
  color:rgb(226, 143, 35);
}

.Price-text{
  font-size: calc(30px + 2vmin);
  color: rgb(214, 211, 211);
  text-align: center;
  padding-top: 4%;
  font-weight: 550;
  font-family:  'Allan';
}
.Price-text:hover{
  color: rgb(230, 230, 230);;
}

.Price-line{
  align-self: center;
  display:flex;
  color: rgb(0, 0, 0);
  justify-content: center;
  font-size: calc(30px + 2vmin);
  text-align: center;
  padding: 1%;
  font-weight: 550;
  font-family:  'Allan';
  background: rgb(196, 133, 16);
  margin-top: 2%;
  margin-left: 35%;
  margin-right: 35%;
  margin-bottom: 5%;
  border-radius: 50px;
}
.Price-line:hover{
  background-color: rgb(226, 143, 35);
}

.Items{
  display: flex;
}

.Items-text{
  font-size: calc(20px + 2vmin);
  color: rgb(230, 230, 230);
  font-weight: 550;
  font-family:  'Allan';
  padding-top: 2%;
  /* border: 5px solid black; */
  width: 100%;
  
}

.Inner-point{
  padding-left: 10%;
  padding-top: 0%;
  font-size: calc(5px + 2vmin);
}

.Bulletin{
  width:50px;
  height:50px;
}

#Bulk-chicken{
  width: 500px;
  height: 500px;
}
a{
  text-decoration: none;
}
.Order{
  align-self: center;
  display:flex;
  color: rgb(0, 0, 0);
  justify-content: center;
  font-size: calc(30px + 2vmin);
  text-align: center;
  padding: 1%;
  font-weight: 550;
  font-family:  'Allan';
  background: rgb(196, 133, 16);
  border-radius: 10px;
}

.App-details{
  display: inline;
  align-items: center;
  justify-content: center;
  font-size: calc(2px + 2vmin);
  color: rgb(230, 230, 230);
  min-width: 100%;
  
}

.Details-header{
  font-size: calc(3px + 2vmin);
  
}

.Loc-data{
  display: flex;
}

#Loc-icon{
  margin-left: 2%;
  width: 50px;
  height: 50px;
}

.Details-text{
  margin-left:4% ;
}

#Phone-icon{
  margin-left: 20%;
  width: 50px;
  height: 50px;
}

.Phone-text{
  margin-left: 5%;
}

.Footer{
  background-color: #1b1d24;
  margin-top: 4%;
  padding-top: 3%;
  padding-left: 1.5%;
  padding-bottom: 15%;
}
.Copy-right{
  background-color: #13151a;
  width: 100%;
  color: antiquewhite;
  text-align: center;
  padding-top: 2%;
  padding-bottom: 5%;
}